.contact-outer {
  justify-content: center;
  align-items: center;
  display: flex;
}

.contact-inner {
  opacity: 0;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  min-width: 80%;
  max-width: 100%;
  display: grid;
}

.contact-title {
  color: var(--gray-text-color);
  font-size: 6em;
}

.contact-inner > div {
  position: relative;
}

.contact-background-text {
  color: var(--bg-text-color);
  font-size: 8em;
  font-weight: bold;
  position: absolute;
  transform: translate(-3rem, -6rem);
}

.contact-info-text {
  color: #fff;
  font-size: 2em;
  position: absolute;
}

